// Main
@siteWidth:960px; // ширина сайта, если сайт резиновый то указывать в процентах
@siteMinWidth:0;  // минимальная ширина сайта
@siteMaxWidth:1920px; // максимальная ширина сайта

// Fonts
@bodyFont:Arial, Tahoma, Verdana, sans-serif; // название шрифта.
@bodyFontSize:14px; // размер шрифта
@bodyFontLineHeight:20px; // межстрочный интервал

@arial: Arial, Tahoma, serif;
@tahoma: Tahoma, serif;
@roboto: "Roboto", serif;

// Colors
@bodyColor:#111; // цвет текста на сайте
@bodyBackground:#f5f5f5; // цвет фона сайта
@bodyLinkColor:#1270b3; // цвет ссылок
@bodyLinkColorHover:#1370b3; // цвет ссылок при наведении

@green:#77ce4e;
@lightGreen:lighten(#98ff98, 10%);


@defaultIndent: 20px;

/*---------- Index styles ----------*/
* {margin: 0;padding: 0;}
header, nav, section, article, aside, footer, figcaption, figure, details, hgroup {display: block;}
audio,canvas,video {display: inline-block;}
html {height: 100%;font-size: 100%;-webkit-text-size-adjust: 100%;-ms-text-size-adjust: 100%;}

body {
  font-family: @bodyFont;
  font-size:@bodyFontSize;
  line-height: @bodyFontLineHeight;
  color: @bodyColor;
  background: @bodyBackground;
  width: 100%;
  height: 100%
}

a{color:@bodyLinkColor;outline:none;text-decoration:underline;}
a:focus,a:hover{color:@bodyLinkColorHover;text-decoration: none;}
img {border:none;}
ul, ol {margin:0;padding:0;}

p {
  margin: 0 0 10px;
}

input, textarea, select {font-family:@bodyFont;font-size:@bodyFontSize;color:@bodyColor;}
input {vertical-align: middle;}

.clear {clear: both;}
.clearfix:after {display: block;content: "";clear: both;}

.box-sizing {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
h1 {
  font-size:@bodyFontSize*2.4;
  font-weight: normal;
  height: @bodyFontSize*2.4;
  line-height: @bodyFontSize*2.4;
  padding-bottom:@defaultIndent/1.5;
}
h2 {
  font-size: @bodyFontSize*2;
  font-weight: normal;
  height: @bodyFontSize*2;
  line-height: @bodyFontSize*2;
  padding-bottom:@defaultIndent/1.5;
}
h3 {
  font-size: @bodyFontSize*1.75;
  font-weight: normal;
  height: @bodyFontSize*1.75;
  line-height: @bodyFontSize*1.75;
  padding-bottom:@defaultIndent/1.5;
}
h4 {
  font-size: @bodyFontSize*1.4;
  font-weight: normal;
  height: @bodyFontSize*1.4;
  line-height: @bodyFontSize*1.4;
  padding-bottom:@defaultIndent/2;
}
h5 {
  font-size: @bodyFontSize*1.3;
  font-weight: normal;
  height: @bodyFontSize*1.3;
  line-height: @bodyFontSize*1.3;
  padding-bottom:@defaultIndent/2;
}
h6 {
  font-size: @bodyFontSize;
  font-weight: bold;
  height: @bodyFontSize;
  line-height: @bodyFontSize;
  padding-bottom:@defaultIndent/2;
}
.border-radius (@borderRadius, 5px){
  -webkit-border-radius:  @borderRadius;
  -moz-border-radius:     @borderRadius;
  -ms-border-radius:      @borderRadius;
  -o-border-radius:       @borderRadius;
  -khtml-border-radius:   @borderRadius;
  border-radius:          @borderRadius;
}
.shadow (@shadow-param) {
  -moz-box-shadow:    @shadow-param;
  -webkit-box-shadow: @shadow-param;
  -ms-box-shadow:     @shadow-param;
  -o-box-shadow:      @shadow-param;
  -khtml-box-shadow:  @shadow-param;
  box-shadow:         @shadow-param;
}
.text-shadow (@text-shadow-param) {
  text-shadow: @text-shadow-param;
}
.gradient (@first-color, @second-color) {
  background: @first-color;
  background-image: -moz-linear-gradient(top, @first-color, @second-color);
  background-image: -ms-linear-gradient(top, @first-color, @second-color);
  background-image: -o-linear-gradient(top, @first-color, @second-color);
  background-image: -webkit-gradient(linear, center top, center bottom, from(@first-color), to(@second-color));
  background-image: -webkit-linear-gradient(top, @first-color, @second-color);
  background-image: linear-gradient(top, @first-color, @second-color);
}


